import {  
    mapMutations  
} from 'vuex';
import 	interfaces from '../../utils/interface.js'



export default {
	data() {
		return {

		};
	},
	methods: {
		...mapMutations(['logout']),

		navTo(url) {
			// this.$api.msg(`跳转到${url}`);
			uni.navigateTo({
				url
			})
		},
		//退出登录
		toLogout() {
			uni.showModal({
				content: '确定要退出登录么',
				success: (e) => {
					if (e.confirm) {
						this.request({
							url: interfaces.login_out, //退出
							method: "POST",
							success: ((res) => {
								if(res.error_code ==0){ //成功退出
									this.logout(); //触发全局state
									setTimeout(() => {
										uni.navigateBack();
									}, 200)
								}
							})
						});










					}
				}
			});
		},
		//switch
		switchChange(e) {
			let statusTip = e.detail.value ? '打开' : '关闭';
			// this.$api.msg(`${statusTip}消息推送`);
		},

	}
}
